import React, { useState } from 'react';
import Header from '../components/Header';
import ReactScan from '../components/Scanner';
import axios from "axios";
import "./Scanner.css";

const QRCodeReader = () => {
    const [scanResult, setScanResult] = useState(null);
    const [messages, setMessages] = useState([]);
    const [scanStatus, setScanStatus] = useState(''); // New state to hold success or failure status

    const handleOnScan = async (result) => {
        if (result) {
            const bookingId = result[0].rawValue;
            try {
                const response = await axios.get(bookingId, {
                    headers: { 'auth': document.cookie.replace(/(?:(?:^|.*;\s*)auth\s*=\s*([^;]*).*$)|^.*$/, "$1") }
                });
                setMessages([response.data]);
                setScanStatus('success'); // Set status to success
            } catch (error) {
                const errorMsg = error.response ? error.response.data.message : 'An error occurred';
                setMessages([errorMsg]);
                setScanStatus('failure'); // Set status to failure
            }
            setScanResult(bookingId);
        }
    };

    const handleNewScan = () => {
        setScanResult(null);
        setMessages([]);
        setScanStatus(''); // Reset status for a new scan
    };

    return (
        <div className={`slot-container ${scanStatus}`}>
            <Header />
            <ReactScan onScan={handleOnScan} />
            {/* <div className="scan-result">
                {scanResult && (
                    <pre>{JSON.stringify(scanResult, null, 2)}</pre>
                )}
                {messages.length > 0 && (
                    <div className="messages">
                        {messages.map((msg, index) => (
                            <p key={index}>{msg}</p>
                        ))}
                    </div>
                )}
            </div> */}
            <button onClick={handleNewScan} className="new-scan-button">
                New Scan
            </button>
        </div>
    );
};

export default QRCodeReader;
